		}
	}
}

@media (max-width: 991.98px) {
	.main-calendar .fc-view>table>tbody>tr>td {
		&:first-child {
			border-left-width: 0;
		}

		&:last-child {
			border-right-width: 0;
		}
	}
}

@media (prefers-reduced-motion: reduce) {
