		transition: none;
	}
}

@media (min-width: 576px) {
	.main-calendar .fc-view.fc-agenda-view .fc-day-header>a>span:first-child {
		font-size: 11px;
		font-weight: 600;
	}
}

@media (min-width: 576px) {
	.main-calendar .fc-view.fc-agenda-view .fc-day-header>a>span:last-child {
		font-size: 25px;
	}
}

@media (min-width: 992px) {
	.main-calendar .fc-view {

		&.fc-listMonth-view .fc-scroller,
		&.fc-listWeek-view .fc-scroller {
			padding: 0;
		}
	}
}

@media (min-width: 576px) {
	.main-calendar .fc-view {

		&.fc-listMonth-view .fc-list-heading,
		&.fc-listWeek-view .fc-list-heading {
			flex: 0 0 15%;
			max-width: 15%;
		}
	}
}

@media (min-width: 768px) {
	.main-calendar .fc-view {

		&.fc-listMonth-view .fc-list-heading,
		&.fc-listWeek-view .fc-list-heading {
			flex: 0 0 12%;
			max-width: 12%;
		}
	}
}

@media (min-width: 992px) {
	.main-calendar .fc-view {

		&.fc-listMonth-view .fc-list-heading,
		&.fc-listWeek-view .fc-list-heading {
			flex: 0 0 10%;
			max-width: 10%;
		}
	}
}

@media (min-width: 1200px) {
	.main-calendar .fc-view {

		&.fc-listMonth-view .fc-list-heading,
		&.fc-listWeek-view .fc-list-heading {
			flex: 0 0 8%;
			max-width: 8%;
		}
	}
}

@media (min-width: 576px) {
